    display: flex;
    justify-content: space-evenly;
    width: 75vw;
    justify-self: center;
    margin: auto;
}

#gameboard > div {
    border: solid 2px black;
    min-width: 600px;
    min-height: 600px;
    max-height: 600px;
    max-width: 600px;


    background-color: black;
    display: flex;
    flex-wrap: wrap;
    gap: 2px;

}

.grid-square {
    width: 58px;
    height: 58px;
}

.empty-square {
    background-color: skyblue;
}

.player-ship {
    background-color: green;
}

.hit-square {
    background-color: red;
}

.missed-square {
    background-color: gray;
}

#computerboard .empty-square:hover {
    background-color: aqua;
}

#score {
    display: flex;
    justify-content: space-evenly;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,6BAA6B;IAC7B,WAAW;IACX,oBAAoB;IACpB,YAAY;AAChB;;AAEA;IACI,uBAAuB;IACvB,gBAAgB;IAChB,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;;;IAGhB,uBAAuB;IACvB,aAAa;IACb,eAAe;IACf,QAAQ;;AAEZ;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,6BAA6B;AACjC","sourcesContent":["#gameboard {\n    display: flex;\n    justify-content: space-evenly;\n    width: 75vw;\n    justify-self: center;\n    margin: auto;\n}\n\n#gameboard > div {\n    border: solid 2px black;\n    min-width: 600px;\n    min-height: 600px;\n    max-height: 600px;\n    max-width: 600px;\n\n\n    background-color: black;\n    display: flex;\n    flex-wrap: wrap;\n    gap: 2px;\n\n}\n\n.grid-square {\n    width: 58px;\n    height: 58px;\n}\n\n.empty-square {\n    background-color: skyblue;\n}\n\n.player-ship {\n    background-color: green;\n}\n\n.hit-square {\n    background-color: red;\n}\n\n.missed-square {\n    background-color: gray;\n}\n\n#computerboard .empty-square:hover {\n    background-color: aqua;\n}\n\n#score {\n    display: flex;\n    justify-content: space-evenly;\n}"],"sourceRoot":""}]);
